import React from "react";
import { Box, Button, Flex, Image, Link, Spacer } from "@chakra-ui/react";
import Facebook from "./assets/social-media-icons/logo.png";

const NavBar = ({ accounts, setAccounts, onOpen }) => {
  const isConnected = Boolean(accounts[0]);

  return (
    <>
      <Flex
        justify="space-between"
        align="center"
        padding="30px"
        className="HeaderBox"
      >
        {/* Left Side - Social Media Icons */}
        <Flex
          justify="space-around"
          width="40%"
          padding="0 75px"
          className="Logo"
        >
          <Link href="https://makercollective.xyz/">
            <Image
              src={Facebook}
              boxSize="142px"
              margin="0 15px"
              width="242px"
              height="49px"
            />
          </Link>
        </Flex>

        {/* Right Side - Sections and connect */}

        <Flex
          justify="space-around"
          align="center"
          width="40%"
          padding="30px 30px 30px 30px"
          className="Navvy"
        >
          <Box
            className="Home"
            margin="0 15px"
            fontFamily="Inter"
            fontSize="25px"
          >
            <a className="Home" href="https://makercollective.xyz/">
              Home
            </a>
          </Box>
          <Spacer />

          {/* Connect */}
          {isConnected ? (
            <Box
              className="ConnectedBtn"
              margin="0 15px"
              fontFamily="Inter"
              fontSize="25px"
              color="#1156DB"
            >
              {accounts[0].substring(0, 6) +
                "..." +
                accounts[0].substring(38, 42)}
            </Box>
          ) : (
            <Button
              className="ConnectBtn"
              backgroundColor="#2D74FF"
              borderRadius="83px"
              color="white"
              cursor="pointer"
              fontFamily="Inter"
              margin="0 15px"
              border="0"
              onClick={onOpen}
            >
              CONNECT
            </Button>
          )}
        </Flex>
      </Flex>
    </>
  );
};

export default NavBar;
