import { useEffect, useState } from "react";
import { ethers, BigNumber } from "ethers";
import { Box, Button, Flex, Input, Text } from "@chakra-ui/react";
import origamiClubNFT from "./whitelist_flat.json";
import proofs from "./proofs.json";
import inputs from "./input.json";

import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// Test Network
// const origamiClubNFTAddress = '0x75aEA4670672FC20531507bFf133188ce2d8B151';

// Mainnet
// const origamiClubNFTAddress = '0xcb74f6d8abc03fe182590ff0d09550cecc096f30';

// Whitelist test
// const origamiClubNFTAddress = "0x7a5bBF1A9b50575A58DA1edA3D74d7a6f18B83a5";

// Whitelist New
const origamiClubNFTAddress = "0x2beDf3b83d2293942c33f8c56B4D77c1c2920d85";

const MainMint = ({ accounts, setAccounts }) => {
  const [mintAmount, setMintAmount] = useState(5);
  const [num, setNum] = useState(1);
  const [totalMints, setTotalMints] = useState(0);
  const [firstMintPrice, setFirstMintPrice] = useState(0);
  const [extraMintPrice, setExtraMintPrice] = useState(0);
  const [isDisabled, setDisabled] = useState(false);
  const [isOpenToPublic, setOpenToPublic] = useState(false);
  const [proof, setProof] = useState([]);
  const [wlMints, setWlMints] = useState(0);
  const isConnected = Boolean(accounts[0]);

  const notify = async (err) => {
    toast.error(err.substr(0, 250).toUpperCase() + "...", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light"
    });
  };

  const checkMints = async () => {
    if (accounts && accounts[0]) {
      // WL mints
      let wln = 0;
      if (inputs) {
        const res = inputs.filter((inp) => {
          return inp.address.toLowerCase() === accounts[0].toLowerCase();
        });
        if (res && res[0]) {
          setWlMints(res[0].quantity);
          wln = res[0].quantity;
        } else {
          setWlMints(0);
          wln = 0;
        }
      }

      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(
        origamiClubNFTAddress,
        origamiClubNFT,
        provider
      );
      const res = proofs.filter((prof) => {
        return prof.address.toLowerCase() === accounts[0].toLowerCase();
      });
      let fPrice, ePrice;
      const openToPublic = await contract.openToPublic();

      if (!openToPublic && res && res.length > 0) {
        // Whitelisted
        fPrice = await contract.FIRST_MINT_PRICE();
        ePrice = await contract.EXTRA_MINT_PRICE();
        setProof(res[0].proof);
      } else {
        // Public
        fPrice = await contract.FIRST_MINT_PRICE_PUBLIC();
        ePrice = await contract.EXTRA_MINT_PRICE_PUBLIC();
      }
      let n = await contract.freeMintedCount(accounts[0]);
      let total = await contract.totalMintedCount(accounts[0]);
      if (!openToPublic && wln > 0 && total.toNumber() < wln) {
        setMintAmount(wln);
      }
      setOpenToPublic(openToPublic);
      setNum(n.toNumber());
      setTotalMints(total.toNumber());
      setFirstMintPrice(ethers.utils.formatEther(fPrice));
      setExtraMintPrice(ethers.utils.formatEther(ePrice));
      console.log(
        ethers.utils.formatEther(fPrice),
        ethers.utils.formatEther(ePrice)
      );
    }
  };

  async function handleMint() {
    if (window.ethereum) {
      try {
        setDisabled(true);
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const contract = new ethers.Contract(
          origamiClubNFTAddress,
          origamiClubNFT,
          signer
        );
        console.log(mintAmount);
        let response = await contract.mint_540(
          BigNumber.from(mintAmount),
          proof,
          {
            value: ethers.utils.parseEther(
              (
                extraMintPrice * (mintAmount - 1 + num) +
                firstMintPrice * (1 - num)
              ).toString()
            )
          }
        );
        response.wait();
        setNum(1);
        setDisabled(false);
        console.log("response: ", response);
      } catch (err) {
        setDisabled(false);
        console.log("error ", err.message);
        notify(err.message);
      }
    }
  }

  const handleDecrement = () => {
    if (mintAmount <= 1) return;
    setMintAmount(mintAmount - 1);
  };

  const handleIncrement = () => {
    if (mintAmount >= 5) return;
    setMintAmount(mintAmount + 1);
  };

  useEffect(() => {
    if (window.ethereum) {
      checkMints();
    }
  }, [accounts, isDisabled]);

  return (
    <Flex justify="center" align="center" className="boundingBox">
      <Box width="520px" className="boundingBox2">
        <div>

          <Text
            className="Title"
            fontSize="68px"
            fontFamily="Inter"
            fontWeight="bold"
          >

            LightTrails

          </Text>

          <Text
            className="TitleWLCheck"
            fontFamily="Inter"
            fontWeight="bold"
          >

            Whitelist Checker

          </Text>

          {isConnected ? (

          <Text className="Description" letterSpacing="-5.5%" fontFamily="Inter">
            <p>Are you an Origami Club member? View your allocated free mint amount below.</p>
          </Text>
          

        ) : (

          <Text className="Description2" letterSpacing="-5.5%" fontFamily="Inter">
            A 1000 piece generative art series that carries forward the concept of ‘traces’ in the form of light trails that extend outward from individual shapes.
          </Text>

          

        )}
        </div>

        {isConnected ? (
          <div>

            {!isOpenToPublic && (
              <div>
                {/* <Text fontSize="18px" letterSpacing="-5.5%" fontFamily="Inter" className="wlnote">
                  This wallet is whitelisted for {wlMints} free mint
                </Text> */}

                    {(() => {
                      if (wlMints === 0) {
                        return (
                          <Text letterSpacing="-5.5%" fontFamily="Inter" className="wlnote">
                                Oh no! This wallet is not whitelisted  <span role="img" aria-label="sheep">😔</span>
                          </Text>
                        )
                      } else if (wlMints === 1) {
                        return (
                          <Text letterSpacing="-5.5%" fontFamily="Inter" className="wlnote">
                                Yay! This wallet is whitelisted for {wlMints} free mint  <span role="img" aria-label="celebrate">🥳</span>
                          </Text>
                        )
                      } else {
                        return (
                          <Text letterSpacing="-5.5%" fontFamily="Inter" className="wlnote">
                                Yay! This wallet is whitelisted for {wlMints} free mints  <span role="img" aria-label="celebrate">🥳</span>
                          </Text>
                        )
                      } 
                    })()}
              </div>

              
              
            )}


                      <Text letterSpacing="-5.5%" fontFamily="Inter" className="snapdate">
                                Snapshot Date: 16th Dec (11am UTC)
                      </Text>

            
            {(() => {
                      if (!isOpenToPublic) {
                        return (
                          <div className="bounder">
                             <Text
                className="support"
                letterSpacing="-5.5%"
                fontFamily="Inter"
                fontWeight="bold"
              >
                Key Dates
              </Text>
                          <Text letterSpacing="-5.5%" fontFamily="Inter" className="wlnotetitle">
                                Whitelist Mint 
                          </Text>
                          <Text fontSize="18px" letterSpacing="-5.5%" fontFamily="Inter" className="wlnote2">
                                22nd of December at 12pm UTC 
                          </Text>
                          <Text letterSpacing="-5.5%" fontFamily="Inter" className="wlnotetitle2">
                                Public Mint
                          </Text>
                          <Text fontSize="18px" letterSpacing="-5.5%" fontFamily="Inter" className="wlnote2">
                                22nd of December at 8pm UTC 
                          </Text>

                          <Text fontSize="18px" letterSpacing="-5.5%" fontFamily="Inter" className="wlnote3">
                         <a class="checkerlink" href="https://opensea.io/collection/origami-club-by-makercollective">Become an Origami Club Member to mint for free {'\u27F6'}</a>
                          </Text>
                          </div>
                        )
                      }  
                    })()}
                    
    
          </div>
        ) : (
          <Text
            marginTop="70px"
            fontSize="25px"
            letterSpacing="-5.5%"
            fontFamily="Inter"
            color="#2D74FF"
            className="pconnect"
          >
            Connect your wallet to check if you are whitelisted
          </Text>
        )}
      </Box>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </Flex>
  );
};

export default MainMint;
